.container{
    
    display: flex;
    flex-direction: column;
    align-items: center;
    font-family: sans-serif;
}

form{
    width: 400px;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: brown;
    border-radius: 3px;
    padding: 10px;
}

form>input{
    padding: 10px;
    margin: 10px 20px;
    border-radius: 4px;
    width: 80%;
}

.box{
    max-width: 350px;
    background-color: #512B81;
    border-radius: 4px;
    margin: 40px;
    padding: 20px;
}

h3{
    margin-top: 5px;
    font-size: 24px;
}
.des{
    border: 1px solid black;
    text-align: start;
    margin: 10px;
    padding: 10px;
    
}
button{
    width: 100px;
    padding: 10px;
    margin: 5px 10px;
    background-color: #61677A;
    border-radius: 4px;
}